.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

.App-header {
}

.App-header h1 {
  color: #61dafb;
  margin: 0;
}

.App-header .logo {
  max-height: 80px;
  width: auto;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.error-message {
  color: #d32f2f;
  padding: 20px;
  text-align: center;
  font-size: 1.2em;
  background-color: #ffebee;
  border-radius: 4px;
  margin: 20px;
}

/* Mobile landscape styles */
.App-mobile-landscape {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

.App-header-mobile-landscape {
  height: 50px;
  background-color: #282c34;
  position: relative;
  display: flex;
  align-items: center;
}

.logo-mobile {
  height: 40px;
  object-fit: contain;
} 